//리스트 찍는 component
function AAAA(props) {
  return (
    <div className="col-md-4 mt-5">
      <img src={ props.toy.img } width="100%" />
      {/* <img src={ process.env.PUBLIC_URL + '/img/car'+ props.toy.id +'.jpg'} width="100%" /> */}
      <h4>{props.toy.title}</h4>
      <p>{props.toy.content}</p>
      <p>{props.toy.price}</p>
    </div>
  )
}

