import React, { useState, useEffect } from 'react';
import { useHistory,useParams } from 'react-router-dom';
import './Detail.scss';
import styled from 'styled-components';
let 박스 = styled.div`
    padding:20px;
`;
let 제목 = styled.h4`
    font-size:25px;
    color:${ props => props.색상 }
`;

function Detail(props) {
    let [put, putchg] = useState('');
    let [visible, visiblechange] = useState(true); 

    useEffect(() => {
      let 타이머 = setTimeout(() => { visiblechange(false) } , 2000 );
      return () => { clearTimeout(타이머) }
      },[visible] //visible 상태가 변경될 때만 실행
    );
    
    let {id} =useParams();
    let his = useHistory();
    return (
          <div className="container">
              {/* styled-components */}
            <박스>
                <제목 className="red">DETAIL 0</제목>
            </박스> 

            { put }
            <input onChange = { (e) => {putchg(e.target.value)} } />

            { visible === true
              ? ( <div className="my-alert">
                    <p>매진임박</p>
                  </div>
                )
              : null
            }

            <div className="row">
              <div className="col-md-6">
              <img className="mt-5" src={process.env.PUBLIC_URL + '/img/car'+ props.toy[id].id +'.jpg'} width="100%" /> 
                {/* <img src="./public/img/car0.jpg" width="100%"/> */}
                {/* <img className="mt-5" src={props.toy[id].img} width="100%" />  */}
                {/* <img className="mt-5" src={"./img/car"+ props.toy[id].id +".jpg"} width="100%" /> */}
              </div>
              <div className="col-md-6 mt-4">
                <h4 className="pt-5">{props.toy[id].title}</h4>
                <p>{props.toy[id].content}</p>
                <p>{props.toy[id].price}</p>

                <Amount 재고={props.재고}></Amount>

                <button className="btn btn-danger" onClick={ () => { props.재고변경([9,11,12])  } }>주문하기</button>
                <button className="btn btn-danger" style={{ display:'block', marginTop:'70%' }}
                    onClick={() => {his.goBack()}}>뒤로가기
                </button>
              </div>
            </div>
          </div>
    )
  }

  function Amount(props) {
    return (<p>남은수량 : { props.재고[0] } </p>)
  }
  
  export default Detail;