import React from 'react';
import { Carousel,Navbar,Nav,NavDropdown,Form,FormControl,Button} from 'react-bootstrap';
import { Link } from 'react-router-dom';

function TOP() {
    return (
        <div>
        <Carousel style={{ width: '100%', height: '100px', background:'grey' }}>
            <Carousel.Item interval={2000}>
            <img
              style={{ width: '100%', height: '100px' }}
              className="d-block w-100"
              src={process.env.PUBLIC_URL + "./img/baner1.jpg"} 
              alt="First slide"
            />
            <Carousel.Caption>
              <h2>First slide</h2>
            </Carousel.Caption>
          </Carousel.Item>
          <Carousel.Item >
          {/* <Carousel.Item interval={2000}> */}
            <img
              style={{ width: '100%', height: '100px' }}
              className="d-block w-100"
              src={process.env.PUBLIC_URL + "./img/baner2.jpg"}
              alt="Second slide"
            />
            <Carousel.Caption>
              <h2>Second slide</h2>
            </Carousel.Caption>
          </Carousel.Item>
          <Carousel.Item>
          {/* <Carousel.Item interval={2000}> */}
            <img
              style={{ width: '100%', height: '100px' }}
              className="d-block w-100"
              src={process.env.PUBLIC_URL + "./img/baner3.jpg"}
              alt="Third slide"
            />
            <Carousel.Caption>
              <h2>Third slide</h2>
            </Carousel.Caption>
          </Carousel.Item>
        </Carousel>
  
      <Navbar bg="light" expand="lg" style={{ paddingLeft: '50px' }}>
        <Navbar.Brand>ToyShop</Navbar.Brand>
        <Navbar.Toggle aria-controls="navbarScroll" />
        <Navbar.Collapse id="navbarScroll">
          <Nav
            className="mr-auto my-2 my-lg-0"
            style={{ maxHeight: '100px' }}
            navbarScroll
          >
            <Nav.Link as={Link} to="/">Home</Nav.Link>
            <Nav.Link as={Link} to="/Detail">Detail</Nav.Link>
            <NavDropdown title="Link" id="navbarScrollingDropdown">
              <NavDropdown.Item href="#action3">Action</NavDropdown.Item>
              <NavDropdown.Item href="#action4">Another action</NavDropdown.Item>
              <NavDropdown.Divider />
              <NavDropdown.Item href="#action5">Something else here</NavDropdown.Item>
            </NavDropdown>
            <Nav.Link style={{ marginLeft: '50px' }} href="#" disabled>
              Link
            </Nav.Link>
          </Nav>
          <Form className="d-flex">
            <FormControl
              type="search"
              placeholder="Search"
              className="mr-2"
              aria-label="Search"
            />
            <Button variant="outline-success" style={{ marginLeft: '20px' }}>Search</Button>
          </Form>
        </Navbar.Collapse>
      </Navbar>
      </div>
    )
  }

export default TOP;